<template>
    <div id="filter-item" class="filter-item-panel">
        <div class="filter-item-wrapper">
            <label class="filter-item-label">
                <input type="checkbox" class="filter-item-checkbox"
                v-model="checked"
                @change="handleSelectedCheckbox">{{ getItemName }}
            </label>
        </div>
    </div>
</template>
<script>
export default {
    name: 'FilterItem',
    props: {
        item: {
            type: String
        }
    },
    data() {
        return {
            checked: true
        }
    },
    computed: {
        getItemName() {
            if (this.item < 0) {
                return 'All Group'
            } else {
                return `Group - ${ this.item }`
            }
        }
    },
    methods: {
        handleSelectedCheckbox() {
            this.$emit('select-checkbox', this.item, this.checked);
        }
    }
}
</script>
<style>
.filter-item-panel {
    position: relative;
    width: 100%;
    height: 35px;
}
.filter-item-wrapper {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.filter-item-label {
    font-size: 20px;
    user-select: none;
}
.filter-item-checkbox {
    margin: 0 7px;
    font-size: 20px;
    vertical-align: middle;
}
</style>
