<template>
    <div v-if="isImageButton" id="top-button" class="button-panel"
    @click="handleSelectedButton">
        <div class="button-image" :class="type"></div>
    </div>
    <div v-else id="top-button" class="text-button-panel"
    @click="handleSelectedButton">
        <div class="text-button-image-panel">
            <div class="text-button-image" :class="type"></div>
        </div>
        <div class="text-button-label">
            <div class="text-button">{{ buttonText }}</div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'TopButton',
    props: {
        type: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            isPressed: false
        }
    },
    methods: {
        handleSelectedButton() {
            if (this.type !== window.CONSTANTS.TOP_BUTTON_TYPE.PLUS ||
                this.type !== window.CONSTANTS.TOP_BUTTON_TYPE.MINUS) {
                this.isPressed = !this.isPressed;
            }
            this.$emit('select-button', this.type);
        }
    },
    computed: {
        isImageButton() {
            return this.type === window.CONSTANTS.TOP_BUTTON_TYPE.PLUS ||
                   this.type === window.CONSTANTS.TOP_BUTTON_TYPE.MINUS ||
                   this.type === window.CONSTANTS.TOP_BUTTON_TYPE.FILTER;
        },
        buttonText() {
            let text = '';
            if (this.type === window.CONSTANTS.TOP_BUTTON_TYPE.SOUND_SET_UP) {
                text = 'SOUND SET-UP';
            } else if (this.type === window.CONSTANTS.TOP_BUTTON_TYPE.BROADCAST) {
                text = 'BROADCAST';
            }
            return text;
        }
    },
    created() {
        console.log("top button", this.type);
    }
}
</script>
<style>
.button-panel {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 40px;
    margin: 7.5px;
    cursor: pointer;
}
.button-image {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: 105%;
    background-position: center center;
    background-repeat: no-repeat;
    background-color: rgb(85, 185, 250);
    box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.1);
    border-radius: 50%;
}
.button-image.plus {
    background-image: url('../assets/imgs/icon-plus.svg');
}
.button-image.minus {
    background-image: url('../assets/imgs/icon-minus.svg');
}
.button-image.filter {
    background-image: url('../assets/imgs/icon-filter.svg');
}
.button-image.plus:active {
    background-color: white;
    background-image: url('../assets/imgs/icon-plus(active).svg');
}
.button-image.minus:active {
    background-color: white;
    background-image: url('../assets/imgs/icon-minus(active).svg');
}
.button-image.filter.pressed {
    background-color: white;
    background-image: url('../assets/imgs/icon-filter(active).svg');
}
.text-button-panel {
    position: relative;
    display: inline-block;
    width: 150px;
    height: 40px;
    border-radius: 50px;
    background-color: white;
    margin: 7.5px;
    cursor: pointer;
}
.text-button-image-panel {
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50px;
}
.text-button-image {
    width: 100%;
    height: 100%;
    background-size: 115%;
    background-position: center center;
    background-repeat: no-repeat;
}
.text-button-image.sound_set_up {
    background-image: url('../assets/imgs/icon-add.svg');
}
.text-button-image.broadcast {
    background-image: url('../assets/imgs/icon-broadcast.svg');
}
.text-button-label {
    position: absolute;
    width: 110px;
    height: 40px;
    background-color: white;
    border-radius: 50px;
    left: 40px;
    box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.1);
}
.text-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 14px;
    color: rgb(12, 100, 158);
    font-weight: bold;
    user-select: none;
}
</style>
