const _getSpeakers = () => {
    let speakers = [];
    for(let i = 0; i < 15; i++) {
        const randomTags = Math.round(Math.random() * 3); //0 ~ 2
        const randomLocationX =Math.round(Math.random() * (10 + i));
        const randomLocationY =Math.round(Math.random() * (10 + i));
        const speaker = {
            id: `aaabbb${i}`,
            name: `speaker-${i}`,
            tags: [randomTags.toString()],
            custom: {
                map_location: {
                    x: 89.47265625 + randomLocationX,
                    y: 43.13771481219632 + randomLocationY
                }
            }
        };
        speakers.push(speaker);
    };
    return speakers;
}